<template>
  <div class="details-tab">
    <el-row>
      <el-col :span="24">
        <div class="grid-content">
          <h3>基本信息</h3>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="4" :class="isRow(index+1,'col')" v-for="(item,index) in baseList" :key="index">
        <div :class="isRow(index+1,'')">{{item[0]}}</div>
        <div :class="isRow(index+1,'')">{{item[1]}}</div>
      </el-col>
    </el-row>

    <el-row style="margin:20px 0">
      <el-col :span="24">
        <div class="grid-content">
          <h3>项目列表</h3>
        </div>
      </el-col>
    </el-row>
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column prop="name" label="项目名称" width="180"></el-table-column>
      <el-table-column prop="address" label="属性"></el-table-column>
      <el-table-column prop="des" label="描述"></el-table-column>
      <el-table-column prop="des" label="状态"></el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="handleCreate('detail')">查看</el-button>
          <el-button type="text" size="small" @click="handleCreate('edit')">删除</el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-row style="margin:20px 0">
      <el-col :span="24">
        <div class="grid-content">
          <h3>子账号列表</h3>
        </div>
      </el-col>
    </el-row>
    <el-table :data="tableData" style="width: 100%;">
      <el-table-column prop="name" label="手机号码" width="180"></el-table-column>
      <el-table-column prop="address" label="姓名"></el-table-column>
      <el-table-column prop="des" label="部门"></el-table-column>
      <el-table-column prop="des" label="状态"></el-table-column>
      <el-table-column fixed="right" label="操作" width="150">
        <template slot-scope="scope">
          <el-button type="text" size="small" @click="handleCreate('edit')">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>


<script>
import { mapGetters, mapActions, mapState } from "vuex";
import _ from "lodash";
export default {
  watch: {
    // flowData(val) {
    // }
  },
  props: {
    // data: {
    //   type: Object,
    //   default: false
    // }
  },
  components: {},
  data: function() {
    return {
      baseList: [
        ["账号", "ID"],
        ["AxureUX", "8447466"],
        ["手机号码", "公司名称"],
        ["18888888888", "睿帆知数"],
        ["子账号数量", "已使用"],
        ["7", "9"]
      ],
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        },
        {
          date: "2016-05-02",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1518 弄"
        },
        {
          date: "2016-05-04",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1517 弄"
        },
        {
          date: "2016-05-01",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1519 弄"
        },
        {
          date: "2016-05-03",
          name: "王小虎",
          address: "上海市普陀区金沙江路 1516 弄"
        }
      ]
    };
  },
  computed: {
    //...mapState([""])
  },
  mounted() {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed: function() {},
  methods: {
    //...mapActions([""]),
    isRow(val, type) {
      if (type == "col") {
        return val % 2 == 0 ? "v-gutter-right" : "v-gutter-left";
      }

      if (!type) {
        //grid-content bg-purple box-border
        return val % 2 == 0
          ? "grid-content box-border"
          : "grid-content bg-purple box-border";
      }
    },
    handleCreate(val) {
      if (val == "edit") {
        this.addDialogFormVisible = true;
        this.formType = val;
      }

      if (val == "detail") {
        this.$router.push({ path: "userdetail" });
      }
    }
  }
};
</script>

<style lang="scss">
.details-tab {
  padding: 10px;

  .el-divider--horizontal {
    margin-top: 10px !important;
  }

  .el-row {
    // margin-bottom: 20px;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .el-col {
    // border-radius: 4px;
  }

  .el-col-4 {
    margin-top: 15px;
  }

  .v-gutter-left {
    padding-left: 25px;
  }

  .v-gutter-right {
    padding-right: 25px;
  }
  .bg-purple {
    // background: #d3dce6;
    background: #e5e9f2;
  }
  .bg-purple-light {
    // background: #e5e9f2;
  }

  .box-border {
    border: 1px solid #d3dce6;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .grid-content {
    display: flex;
    align-items: center;
    // border-radius: 4px;
    min-height: 36px;
  }
  .row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
  }
}
</style>



