<template>
  <div class="user-detail">
    <el-tabs
      v-model="activeName"
      style="margin-top:15px;"
      type="border-card"
      @tab-click="showCreatedTimes(activeName)"
    >
      <el-tab-pane
        v-for="item in tabMapOptions"
        :key="item.key"
        :label="item.label"
        :name="item.key"
      >
        <keep-alive>
          <component :is="setTabTemplate(activeName)"></component>
        </keep-alive>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>


<script>
import { mapGetters, mapActions, mapState } from "vuex";
// import _ from "lodash";
import tab from "./tab";
import tab2 from "./tab2";
import tab3 from "./tab3";
export default {
  watch: {
    // flowData(val) {
    // }
  },
  props: {
    // data: {
    //   type: Object,
    //   default: false
    // }
  },
  components: {},
  data: function() {
    return {
      tabMapOptions: [
        { label: "用户详情", key: "tab" },
        { label: "编辑资料", key: "tab2" },
        { label: "登陆日志", key: "tab3" }
      ],
      activeName: "tab"
    };
  },
  computed: {
    //...mapState([""])
  },
  mounted() {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  destroyed: function() {},
  methods: {
    //...mapActions([""]),
    showCreatedTimes(val) {},
    setTabTemplate(val) {
      switch (val) {
        case "tab":
          return tab;
        case "tab2":
          return tab2;
        case "tab3":
          return tab3;
        default:
          "";
      }
    }
  }
};
</script>

<style lang="scss">
.user-detail {
  position: absolute;
  width: 100%;
}
</style>



